.Instructions {
  background: -webkit-gradient(linear, left top, left bottom, from(#02547D), to(#1DF7A9));
  background: -webkit-linear-gradient(#02547D, #1DF7A9);
  background: -o-linear-gradient(#02547D, #1DF7A9);
  background: linear-gradient(#02547D, #1DF7A9);
  /* background-image: url("https://wallpapercave.com/wp/Ou1L18s.jpg"); */
  /* background-size: 100%; */
  /* height: 50%; */
  padding: 20px 20px;
  /*border-radius: 15px;*/
  /*margin-top: 100px;*/
  width: 100%;
  text-align: center;
  font-size: 5rem;
  color: rgb(241, 241, 241);
  text-shadow: 1px 1px 15px black;
}

.Instructions:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}



