.card {
  /*background: rgb(245, 0, 0);*/
  margin-left: 50px;
  border: 20px;
  border-color: black;
  border-radius: 10px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
          box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
  text-align: left;
}

.card>.img-container {

  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: -webkit-radial-gradient(#001D29, #93B8C2);
  background: -o-radial-gradient(#001D29, #93B8C2);
  background: radial-gradient(#001D29, #93B8C2);
}

.card>.img-container>img {
  width: 80%
}

.card>.img-container:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  /* transform: scale(1.3); */
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  background: -webkit-radial-gradient(#FCFFF5, #91AA9D);
  background: -o-radial-gradient(#FCFFF5, #91AA9D);
  background: radial-gradient(#FCFFF5, #91AA9D);
}



